import api from "./axiosFetch";
//import axios from "axios";
export default class Auth {
    static async login({ username, password }) {
        console.log(username, password)
        const query = `grant_type=&username=${username}&password=${password}&scope=&client_id=&client_secret=`
        console.log(query)
        return await api.post('/login', query, {})

    }
    static async register({ username, password }) {
        console.log(username, password)
        console.log(`/register?username=${username}&password=${password}`)
        return await api.post(`/register?username=${username}&password=${password}`)
    }
    static async squeeze(url) {
        //const key = localStorage.getItem('access_token')
        //console.log(key)
        console.log(url)

        return await api.post(`/squeeze?link=${url}`)
    }
    static async statistics({ asc_short, offset, limit }) {
        console.log(`/statistics?order=${asc_short}&offset=${offset}&limit=${limit}`)
        return await api.get(`/statistics?order=${asc_short}&offset=${offset}&limit=${limit}`)
    }
    static async keyPath(key) {
        return await api.get(`/s/${key}`)
    }
}