import { useEffect, useState } from "react"
import { login, register } from "../store/storeSlice"
//import  register  from "../utils/Helpers"
import styles from '../App.module.scss'   
import { useDispatch, useSelector } from "react-redux"

export default function Loginform(){
    const store = useSelector(state=>state.authData)
    useEffect(() => {
            
      console.log(store)
    }, [store]);
    const [username, setName] = useState('')
    const [password, setPas] = useState('')
    const dispatch = useDispatch()
    const disL = ()=>{        
        dispatch(login({username,password}))
        setName('')
        setPas('')
    }
    const disR = ()=>{
        dispatch(register({username,password}))
        setName('')
        setPas('')
    }
    
    return (
        <div className={styles.container}>
            <div className={styles.section2__authorization}>
                <div className={styles.section2__authorization__inputs}>
                    <input type="text" placeholder="name" 
                        onChange={e=>setName(e.target.value)}
                        value={username}
                    />
                    <input type="password" placeholder="password" 
                        onChange={e=>setPas(e.target.value)}
                        value={password}
                    />
                </div>
                <div className={styles.section2__authorization__buttons}>
                    <button onClick={disL}></button>
                    <button onClick={disR}></button>
                    <button
                        onClick={()=> {
                            console.log(store)
                        }}
                    >
                        store
                    </button>
                </div>
        
                {store.status==='loading' &&(
                    <div className={styles.error}>
                        <h2>Loading</h2>
                    </div>)}
                {store.error &&(
                    <div className={styles.error}>
                        <h2>{store.error}</h2>
                    </div>)}
                {store.isAuth===true && !store.error &&(
                    <div className={styles.error}>
                        <h2> </h2>
                    </div>)}
                {store.user && !store.error &&(
                    <div className={styles.error}>
                        <h2>{`me: ${store.user.username}`}</h2>
                        <h3>{' '}</h3>            
                    </div>)
                }               
            </div>
        </div>)
}
