import { useForm } from 'react-hook-form'
import { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import styles from '../App.module.scss'    
import { squeeze } from '../store/storeSlice'
import Parameters from './Parameters'


export default function Form(){
  const dispatch = useDispatch()
    //const [url, setURL] = useState('')
    const [copiedLinks,setCopiedLink] = useState(null)
   const copyHandler=(l)=>{
    navigator.clipboard.writeText(l).then(()=> setCopiedLink(l))
   
   }
    const {
        register,
        formState:{errors},
        handleSubmit,
        reset,
    } = useForm({
        mode:'onSubmit'
    })
   
    const onSubmit= ({Url}) => {
                console.log(Url)
                dispatch(squeeze(Url))
                reset()
    }
    const store = useSelector(state=>state.authData)
    const loading = useSelector(state=>state.authData.status)
    console.log(loading)
    // const sendParams = () => {
    //   dispatch(squeeze(url,order,offset,limit,))
    // }
    
    return(
        <div className={styles.container}>
          <form 
          onSubmit={handleSubmit(onSubmit)}
          // onSubmit={(e)=>e.preventDefault()}
          >
            <div className={styles.section1__inputs}>
              <div>
                  <input type="url"
                  // onChange={e=>{      
                  //   //console.log(store)          
                  //   setURL(e.target.value)}
                  // }
                  //value={url}
                  placeholder='  '
                  className={styles.section1__inputs__cutDownInput}
                  {...register('Url',{
                    required:'   ',
                    pattern:{
                        value: '/(http(s)?:.)?(www)?[-a-zA-Z0-9@:%._~#=]{2,256}[a-z]{2,6}\b([-a-zA-Z0-9@:%_.~#?&//=]*)/g',
                        message:'URL  '
                    }
                  })}
                  disabled={loading==='loading'}
                  style={{
                    outlineColor:errors.Url? 'hsl(0.2,94%,57%)' :'currentcolor',
                    outlineWidth: errors.Url? '4px' :'1px'
                  }}
                  />
                  <input type="submit" value='' className={styles.section1__inputs__submit} 
                     //onClick={()=>dispatch(squeeze(url))}
                     disabled={loading==='loading'}
                />
              </div> 
              <div>
                {errors.Url && (
                  <div className={styles.error}>
                     <i>{errors.Url.message}</i>
                  </div>
                )}
              </div> 
            </div>
            <Parameters />
            </form>
            {store.squeeze.length === null ? null : 
                  <div className={styles.error}>
                      {store.squeeze.map(s=>{
                        return (
                          <ol key={s.id} style={{display:'flex'}} >
                            <span style={{width:'70%'}} ><ul style={{padding:0}}><i style={{textTransform:'uppercase',color:'green'}}>target</i>: {s.target}</ul></span>
                            <span 
                              style={{width:'30%',color:'green',fontSize:'20px'}}>
                                <ul style={{padding:0}}>
                                short: {s.short} <button onClick={()=>copyHandler(s.short)}>{
                                  copiedLinks=== s.short?'Copied' : 'Copy'
                                  }</button>
                                </ul>
                            </span>
                          </ol>
                        )
                      })
                      }
                  </div>
              
              }
            {store.statistics && (
               <div>{store.statistics.map(d=>{
                 return( 
                  <ol key={d.id} style={{
                    borderRadius:'5px',
                    backgroundColor: 'rgba(69,99,70,1)'
                    }}>
                    <li><b>counter</b>: {d.counter}</li> 
                      <li><b>id</b>: {d.id}</li>
                      <li><b>short</b>: {d.short}</li>
                      <li><b>target</b>: {d.target}</li>
                  </ol>
                  )                                                  
                  })}                
                </div> )            
              }
            </div>
    )
}