import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import Auth from "../utils/Auth";

const setError = (state, action) => {
    // const typeStringError = action.json()
    //console.log(action.payload.response.data.detail)
    if (action.payload.response.status === 422) {
        console.log(action.payload.response.data.detail[0].msg)
        state.status = 'rejected';
        state.error = action.payload.response.data.detail[0].msg

    } else {
        state.status = 'rejected';
        state.error = action.payload.response.data.detail
    }
    //console.log(action.payload.response.data.detail)
    //console.log(action.payload)
    //state.error = action.payload
}
export const login = createAsyncThunk(
    'user/login',
    async function({ username, password }, { rejectWithValue, dispatch, action }) {
        try {
            const res = await Auth.login({ username, password })
            if (res.code === 'ERR_BAD_REQUEST') {
                throw new Error(res)
            } else if (res.status === 200) {
                console.log(res)
                localStorage.setItem('access_token', res.data.access_token)
                dispatch(setAuth(true))
                return
            } else {
                console.log(' ')
                return
            }
        } catch (error) {
            console.log(error)
            return rejectWithValue(error)
        }

    }
)
export const register = createAsyncThunk(
    'user/register',
    async function({ username, password }, { rejectWithValue, dispatch }) {
        try {

            const res = await Auth.register({ username, password })
            console.log(typeof res)
            console.log(res)
            if (res.code === 'ERR_BAD_REQUEST') {
                throw new Error(res)
            } else if (res.status === 200) {
                //console.log(res)
                dispatch(setUser(res))
                return
            } else {
                console.log(' ')
                return
            }
        } catch (error) {
            console.log(error)
            return rejectWithValue(error)
        }

    }
)
export const squeeze = createAsyncThunk(
    'user/squeeze',
    async function(url, { rejectWithValue, dispatch }) {
        try {
            console.log(url)
            const res = await Auth.squeeze(url)
            if (res.code === 'ERR_BAD_REQUEST') {
                throw new Error(res)
            } else if (res.status === 200) {
                //console.log(res)
                //console.log(res.data)
                dispatch(AddSqueeze(res))
                return
            } else {
                console.log(' ')
                return
            }
        } catch (error) {
            console.log(error)
            return rejectWithValue(error)
        }
    }
)
export const keyPath = createAsyncThunk(
    'user/keyPath',
    async function(key, { rejectWithValue, dispatch }) {
        try {
            const res = await Auth.keyPath(key)
            if (res.code === 'ERR_BAD_REQUEST') {
                throw new Error(res)
            } else if (res.status === 200) {
                dispatch(setStatistics(res))
                return
            } else {
                console.log(' ')
                return
            }
        } catch (error) {
            console.log(error.message)
            return rejectWithValue(error.message)
        }

    }
)
export const statistics = createAsyncThunk(
    'user/statistics',
    async function({ asc_short, offset, limit }, { rejectWithValue, dispatch }) {
        try {
            //console.log(asc_short, limit, offset)
            const res = await Auth.statistics({ asc_short, offset, limit })
            if (res.code === 'ERR_BAD_REQUEST') {
                throw new Error(res)
            } else if (res.status === 200) {
                //console.log(res)
                dispatch(setStatistics(res))
                return
            } else {
                console.log(' ')
                return
            }
        } catch (error) {
            console.log(error)
            return rejectWithValue(error)
        }

    }
)

const authSlice = createSlice({
    name: 'user',
    initialState: {
        user: null,
        squeeze: [],
        isAuth: false,
        status: null,
        error: null,
        statistics: null
    },
    reducers: {
        setAuth(state, action) {
            console.log(typeof action.payload)
            state.isAuth = action.payload
        },
        setUser(state, action) {
            console.log(action)
            state.user = action.payload.data
        },
        AddSqueeze(state, action) {
            console.log(action)
            state.squeeze.push(action.payload.data)
        },
        setStatistics(state, action) {
            console.log(action)
            state.statistics = action.payload.data
        }
    },
    extraReducers: {
        [login.pending]: (state) => {
            state.status = 'loading'
        },
        [login.fulfilled]: (state, action) => {
            state.status = 'resolved';
            state.error = null
        },
        [login.rejected]: setError,
        [register.pending]: (state) => {
            state.status = 'loading'
        },
        [register.fulfilled]: (state) => {
            state.status = 'resolved';
            state.error = null
        },
        [register.rejected]: setError,
        [squeeze.rejected]: setError,
        [squeeze.pending]: (state) => {
            state.status = 'loading'
        },
        [squeeze.fulfilled]: (state) => {
            state.status = 'resolved';
            state.error = null
        },
        [statistics.rejected]: setError,
        [statistics.pending]: (state) => {
            state.status = 'loading'
        },
        [statistics.fulfilled]: (state) => {
            state.status = 'resolved';
            state.error = null
        },

    }
})
const { setAuth, setUser, AddSqueeze, setStatistics } = authSlice.actions
export default authSlice.reducer