import { useState } from "react";
import { useDispatch } from "react-redux"
import Select from 'react-select'
import makeAnimated from 'react-select/animated'
import { statistics } from "../store/storeSlice";

//import '../App.module.scss'   
const options = [
    { value: 'asc_short', label: 'asc_short' },
    { value: 'asc_target', label: 'asc_target' },
    { value: 'asc_counter', label: 'asc_counter' },
    { value: 'desc_short', label: 'desc_short' },
    { value: 'desc_target', label: 'desc_target' },
    { value: 'desc_counter', label: 'desc_counter' }
  ]
 
const animatedComponents = makeAnimated()

export default function Parameters(){
    const dispatch = useDispatch()
    const [order, setOrder] = useState('asc_short');
    const [offset, setOffset] = useState(1);
    const [limit, setLimit] = useState(10);
    // console.log(order)
    // console.log(offset)
    // console.log(limit)
    const sendParams = () => {
        console.log(order,offset,limit)
        dispatch(statistics({asc_short:order,offset,limit}))
    }

    return(
        <div className="section1__parameters">
            <div className='section1__parameters__div1'>
                <span>order</span>  
                <span className='section1__parameters__select'>
                    <Select options={options} 
                        classNamePrefix='customSelect'
                        //isSearchable={false}
                        components={animatedComponents}
                        onChange={(e)=>setOrder(e.value)}
                        // placeholder='order'
                        value={{value:order,label:order}}
                    />
                </span>
            </div>
            <div>
                <div className="">
                    offset
                </div>     
                <input type="number" placeholder="offset" value={offset} onChange={(e)=>{
                    if(e.target.value<0) {
                        
                        return 
                    }else{
                        setOffset(e.target.value)
                    }
                    }}
                />                                        
            </div>
            <div>
                <div className="">
                    limit
                </div>
                <input type="number"  placeholder="limit" value={limit} onChange={(e)=>{
                    if(e.target.value<0) {
                        
                        return 
                    }else{
                        setLimit(e.target.value)
                    }
                    }}/>
            </div>
            <div>
                <input type="submit" value='' className='section1__parameters__statistics' onClick={(e)=>sendParams(e)}/>
            </div>    
        </div>
        
    )
}